<template>
  <div id="LeaveSay">
    <header class="LeaveSay-head">
      <span>留言板</span>
    </header>
    <div class="LeaveSay-input">
      <section class="ly-input-name">
        <el-input
          placeholder="请输入名字"
          v-model="inputname"
          clearable>
        </el-input>
      </section>
      <section class="ly-input-email">
        <el-input
          placeholder="请输入邮箱"
          v-model="inputemail"
          clearable>
        </el-input>
      </section>
      <section class="ly-input-content">
        <el-input
          type="textarea"
          :rows="7"
          placeholder="请输入内容"
          v-model="inputcon">
        </el-input>
      </section>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'LeaveSay',
    data:function() {
      return {
        inputname: '',
        inputemail: '',
        inputcon: ''
      }
    }
  }
</script>

<style>
  #LeaveSay {
    margin: 20px 5% 0px 5%;
    /* min-height: 300px; */
    background-color: #ECECEC;
    padding: 10px;
  }
  .LeaveSay-head {
    margin: 30px 40% 0px 40%;
    /* border: 1px red solid; */
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    color: #55BCC9;
    letter-spacing: 5px;
  }
  .LeaveSay-input {
    margin-top: 20px;
    border: 1px red solid;
  }
  .ly-input-name {
    display: inline-block;
    margin: 30px 0px 0px 8%;
    width: 37%;
    /* border: 1px blueviolet solid; */
  }
  .ly-input-email {
    display: inline-block;
    float: right;
    margin: 30px 8% 0px 0px;
    width: 43%;
  }
  .ly-input-content {
    margin: 30px 8% 0px 8%;
  }
</style>



