<template>
  <div id="app">
    <router-view></router-view>
    <div id="bottom" :style="bottomH">
    </div>
    <header id="guide" :style="bkcolor">
      <blog-guide></blog-guide>
    </header>
  </div>
</template>

<script>
  import Guide from './components/function/Guide.vue'
  export default {
    name: 'App',
    components: {
      'blog-guide': Guide
    },
    data:function() {
      return {
        bkcolor: '',
        bottomH: {
          height:'200px'
        }
      }
    },
    mounted() {
      this.ChangeGuide();
    },
    watch: {
      '$route': 'ChangeGuide'
    },
    methods: {
      ChangeGuide:function() {
        var e = this.$route.path;
        var b = document.getElementById('bottom');
        if(e === '/bloghome') {
          this.bkcolor = {background: ''};
          this.bottomH.height = '200px';
        }
        else {
          this.bkcolor = {backgroundColor: 'white'};
          this.bottomH.height = '50px';
        }
        // console.log(e);
      }
    }
}
</script>

<style>
  body {
    margin: 0px;
    padding: 0px;
    background: url(./assets/300295.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
  #app {

  }
  #guide {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 60px;
    width: 100%;
    /* border: 1px red solid; */
    background: rgba(255,255,255,1);
  }
  #bottom {
    background-color: #303133;
  }
</style>



