<template>
  <div class="blackback">
    <transition name="el-zoom-in-top">
      <section v-show="showintro" class="welcome">
        <article class="intro-p">
          <p class="welcome-p">Welcome &nbsp;to &nbsp;Here</p>
          <p style="font-size: 18px;color: white;">我是努力'向前</p>
          <p style="font-size: 15px;color: white;">别人云亦云，要有自己的思考</p>
        </article>
      </section>
    </transition>
   <div class="Header-recd">
      <section class="Hr-recd-li">
        <aside class="Hr-recd-li-head">
          <i class="el-icon-s-platform"></i>
        </aside>
        <aside class="Hr-recd-li-p">
          <p>前端学习</p>
        </aside>
      </section>
      <section class="Hr-recd-li">
        <aside class="Hr-recd-li-head">
          <i class="el-icon-camera-solid"></i>
        </aside>
        <aside class="Hr-recd-li-p">
          <p>生活记录</p>
        </aside>
      </section>
      <section class="Hr-recd-li">
        <aside class="Hr-recd-li-head">
          <i class="el-icon-more"></i>
        </aside>
        <aside class="Hr-recd-li-p">
          <p>更多文章</p>
        </aside>
      </section>
    </div>
  </div>
</template>

<script>
  export default {
    name:'Header',
    data:function() {
    	return {
        showintro: false
    	}
    },
    mounted() {
      this.showintro = true;
    },
    methods:{
    }
  }
</script>

<style>
  .blackback {
    height: 700px;
    background:rgba(33,33,33,0.2);
  }
  .welcome {
    position: absolute;
    top: 20%;
    left: 3%;
    width: 60%;
    height: 250px;
    /* border: 1px red solid; */
    text-align: left;
    color: white;
  }
  .welcome-p {
    font-size: 60px;
    letter-spacing: 1px;
    line-height: 35px;
    font-weight: bolder;
  }
  .Header-recd {
    /* border: 1px white solid; */
    position: absolute;
    top: 510px;
    left: 0%;
    width: 40%;
    height: 180px;
    display: flex;
    padding: 10px;
  }
  .Hr-recd-li {
    margin-left: 5px;
    flex: 1;
    /* border: 1px red solid; */
    position: relative;
    transition: background-color 0.2s;
    /* background:rgba(33,33,33,0.5); */
  }
  .Hr-recd-li:hover {
    background-color: white;
  }
  .Hr-recd-li-head {
    /* border: 1px red solid; */
    text-align: center;
    font-size: 70px;
    color: white;
  }
  .Hr-recd-li-p {
    text-align: center;
    color: white;
    font-size: 20px;
  }
</style>



