<template>
  <transition name="el-zoom-in-center">
    <li v-show="createArt" id="recd-art-li">
      <aside v-if="!IcoImg" class="recd-art-img">
        <img src="../../../assets/css.png" class="rd-art-icosrc" />
      </aside>
      <section class="recd-art-content">
        <article class="recd-art-p">
          <aside class="recd-art-title">博客创建</aside>
          <aside class="recd-art-con">利用vue-lic作为前端,express后端,mysql数据库开发个人博客,</aside>
          <section class="art-mark-readmore">
            <section class="art-readmore">
              View
              <i class="el-icon-caret-right" style="font-size: 18px;margin-left: 30px;"></i>
            </section>
          </section>
        </article>
      </section>
      <aside v-if="IcoImg" class="recd-art-img">
        <img src="../../../assets/sideback1.jpg"  class="rd-art-imgsrc"/>
      </aside>
    </li>
  </transition>
</template>

<script>
  import icohtml from '@/assets/html.png'

  export default {
    name:'recdart',
    props:['post'],
    data:function() {
      return {
        createArt:false,
        IcoImg:this.post.imgJudge,
        icosrc: '',
        artcontent: '',
        artconlocation: '',
        artimglocation: ''
      }
    },
    mounted() {
      if(this.IcoImg === false) {
        switch(this.post.class) {
          case 'html':this.icosrc = require('../../../assets/html.png');break;
          case 'css':this.icosrc = require('../../../assets/css.png');break;
          case 'javascript':this.icosrc = require('../../../assets/html.png');break;
        }
      }
      this.createArt = true;
    }
  }
</script>

<style>
  #recd-art-li {
    margin: 20px 20% 70px 3%;
    list-style: none;
    /* border: 1px red solid; */
    padding: 1px 10px 0px 0px;
    display: flex;
    border-radius: 10px;
  }
  .recd-art-content {
    flex: 1;
    margin: 10px;
    /* border: 1px green solid; */
    padding: 1px;
  }
  .recd-art-img {
    flex: 1;
    margin-top: 10px;
    width: 40%;
    height: 270px;
    cursor: pointer;
    border-radius: 10px;
    transition: background-color 0.5s;
  }
  .recd-art-img:hover {
    background-color: black;
  }
  .rd-art-imgsrc {
    width: 100%;
    height: 270px;
    border-radius: 10px;
    transition: opacity 0.5s;
  }
  .rd-art-imgsrc:hover {
    opacity: 0.6;
  }
  .rd-art-icosrc {
    margin: 50px 0px 0px 35%;
    /* border: 1px red solid; */
    width: 130px;
    height: 120px;
  }
  .recd-art-p {
    margin: 10px;
   /* border: 1px blue solid; */
    padding: 3px;
  }
  .recd-art-title {
    /* border: 1px red solid; */
    font-size: 27px;
    margin: 5px 0px 5px 0px;
    font-weight: bold;
    color: #00acc1;
  }
  .recd-art-con {
    margin: 20px 0px 5px 0px;
    /* border: 1px green solid; */
    min-height: 130px;
    font-size: 14px;
    letter-spacing: 3px;
    line-height: 30px;
    color: #3e2723;
    font-weight: bold;
  }

  .art-mark-readmore {
    margin: 10px 50% 0px 0%;
    /* border: 1px yellow solid; */
    background-color: #212121;
    height: 40px;
    padding-top: 7px;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.9s;
  }
  .art-mark-readmore:hover {
    background-color: #8bc34a;
  }
  .art-readmore {
    /* border: 1px red solid; */
    font-size: 23px;
    font-weight: bold;
    text-align: center;
    color: white;
  }
</style>



