<template>
  <div id="about-auto">
    <section class="about-head">
      <section class="about-author">
        <transition name="el-zoom-in-center">
          <aside v-show="hello">
            <span style="font-size: 45px;">Hello, everyone!</span>
          </aside>
        </transition>
        <transition name="el-zoom-in-top">
          <aside v-show="name" style="margin-top: 10px;">
            <span style="font-size: 20px;">我是努力'向前</span>
          </aside>
        </transition>
      </section>
    </section>
    <section class="about-con">
      <about-blog></about-blog>
      <leave-say></leave-say>
    </section>
  </div>
</template>

<script>
  import BlogMessage from '../function/About/BlogMessage.vue'
  import LeaveSay from '../function/About/LeaveSay.vue'

  export default {
    name:'BlogAbout',
    components: {
      'about-blog': BlogMessage,
      'leave-say' : LeaveSay
    },
    data:function() {
      return {
        hello: false,
        name: false
      }
    },
    mounted() {
      this.hello = true;
      this.name = true;
    }
  }
</script>

<style>
  #about-auto {
    margin: 70px 0px 0px 0px;
  }
  .about-head {
    margin: 0px;
    height: 270px;
    /* background:rgba(39,39,39,0.3); */
    padding: 10px;
  }
  .about-author {
    /* border: 1px green solid; */
    margin: 80px 63% 0px 7%;
    text-align: left;
    color: white;
    line-height: 30px;
  }
  .about-con {
   /* background:rgba(39,39,39,0.3); */
    /* padding-top: 10px; */
  }
</style>



