<template>
  <div id="blog-guide">
    <section id="author-name" :style="authorColor">
      <span v-if="authorname">Greatiga</span>
      <span v-else style="color: #55BCC9;"><i class="el-icon-s-custom"></i></span>
    </section>
    <section id="guide-name" :style="guidename">
      <router-link to="/bloghome" class="guide-name-p">
        <aside :style="guidecolor[0]">首页</aside>
      </router-link>
      <router-link to="/blogart" class="guide-name-p">
        <aside :style="guidecolor[1]">文章</aside>
      </router-link>
      <router-link to="/blogonroad" class="guide-name-p">
        <aside :style="guidecolor[2]">在路上</aside>
      </router-link>
      <router-link to="/blogdiary" class="guide-name-p">
        <aside :style="guidecolor[3]">日记</aside>
      </router-link>
      <router-link to="/bloglink" class="guide-name-p">
        <aside :style="guidecolor[4]">友链</aside>
      </router-link>
      <router-link to="/blogabout" class="guide-name-p">
        <aside :style="guidecolor[5]">关于博客</aside>
      </router-link>
    </section>
    <div class="say-button">
      <aside class="say"> <i class="el-icon-s-promotion"></i></aside>
      <aside class="say-p">留 言</aside>
    </div>
  </div>

</template>

<script>
  export default {
    name:'Guide',
    data:function() {
      return {
        authorname:true,
        guidecolor:[
          {color: '#8bc34a'},
          {color: 'black'},
          {color: 'black'},
          {color: 'black'},
          {color: 'black'},
          {color: 'black'}
        ],
        guideId:1,
        activeIndex:'1',
        authorColor: { color: 'black'},
        guidename: {
          marginLeft: '32%'
        }
      }
    },
    mounted() {
      this.ChangGuideName();
    },
    watch: {
      '$route': 'ChangGuideName'
    },
    methods: {
      ChangGuideName:function() {
        var e = this.$route.path;
        var n;
        this.guidecolor[this.guideId  - 1].color = 'black';
        switch(e) {
          case '/bloghome': n = 1;break;
          case '/blogart': n = 2;break;
          case '/blogonroad': n = 3;break;
          case '/blogdiary': n = 4;break;
          case '/bloglink': n = 5;break;
          case '/blogabout': n = 6;break;
        };
        this.guideId = n;
        this.guidecolor[n - 1].color = '#8bc34a';
      }
    }
  }
</script>

<style>
  #blog-guide {
    width: 100%;
    height: 45px;
   /* border: 1px black solid; */
    margin-top: 10px;
    position: relative;
  }
  #author-name {
    /* border: 1px yellow solid; */
    position: absolute;
    top: 0px;
    left: 2%;
    font-size: 30px;
    /* color: #55BCC9; */
    cursor: pointer;
  }
  #guide-name {
    height: 40px;
    max-width: 600px;
    /* border: 1px red solid; */
  }
  #guide-serch {
    position: absolute;
    top: 7px;
    left: 80%;
    /* border: 1px yellow solid; */
  }
  .guide-name-p {
    display: inline-block;
    text-align: center;
    margin: 10px 0px 0px 10px;
    width: 80px;
    height: 25px;
    font-size: 14px;
    letter-spacing: 4px;
    cursor: pointer;
    text-decoration: none;
    font-weight: bold;
  }
  .say-button {
    position: absolute;
    top: 0px;
    left: 90%;
    width: 110px;
    height: 40px;
    background-color: #689F38;
    border: 0px;
    border-radius: 15px;
    cursor: pointer;
    padding-top: 1px;
  }
  .say {
    margin-top: 4px;
    margin-left: 15%;
    display: inline-block;
    font-size: 20px;
    color: white;
  }
  .say-p {
    display: inline-block;
    color: white;
  }
</style>



