<template>
	 <div id="home-auto">
      <div id="home-head">
        <blog-head></blog-head>
      </div>
      <div id="home-body-rdblog">
        <Recd-blog></Recd-blog>
      </div>
      <div id="home-more-blog">
        <div class="home-more-blog-back">
          <section class="he-more-button">
            <aside class="he-button-p">
              更多文章
              <i class="el-icon-plus" style="margin-left: 50px;"></i>
            </aside>

          </section>
        </div>
      </div>
      <div id="home-body-onroad">
        <Recd-life></Recd-life>
      </div>
	 </div>
</template>
<script>

  import Header from '../function/Home/Header.vue'
  import Recdblog from './blogbody/Home/recommendblog.vue'
  import Recdlife from './blogbody/Home/recommendlife.vue'

	export default {
		name: 'BlogHome',
		components: {
      'blog-head': Header,
      'Recd-blog': Recdblog,
      'Recd-life': Recdlife
		},
    data:function() {
      return {
        autest:''
      }
    },
    mounted() {
      this.axios.post('/users').then((response)=>{
        console.log(response.data);
        this.autest = response.data;
      }).catch(err=>{
        console.log('No')
      });
    },
    methods:{

    }
	}
</script>

<style>
  #home-auto {
    margin: 0px;
    /* border: 2px white solid; */
  }
  #home-head {
    width: 100%;
    height: 700px;
    position: relative;
  }
  #home-body-rdblog {
    min-height: 500px;
    padding-top: 5px;
    /* border: 1px blue solid; */
    background-color: white;
  }
  #home-more-blog {
    margin: 0px;
    height: 170px;
  }
  .home-more-blog-back {
    margin: 0px;
    height: 170px;
    background: rgba(33,33,33,0.4);
    padding-top: 10px;
  }
  #home-body-onroad {
    margin: 0px;
    background-color: white;
    padding-top: 10px;
  }
  .he-more-button {
    margin: 30px 45% 0px 37%;
    border: 1px white solid;
    height: 80px;
    text-align: center;
    cursor: pointer;
    transition: background-color 2s,margin-right 0.6s;
    padding-top: 1px;
  }
  .he-button-p {
    margin-top: 20px;
    letter-spacing: 3px;
    color: white;
    font-size: 27px;
    font-weight: bold;
  }
  .he-more-button:hover {
    background-color: #212121;
    margin-right: 37%;
  }
</style>



