<template>
  <div id="article-auto">

    <div class="article-side-left">
      <aside class="side-left-title">
        <h1>努力'向前</h1>
      </aside>
      <aside class="side-left-con">
        <p>算不上竭尽全力，但至少努力的在往前走</p>
      </aside>
    </div>

    <div class="article-body">
      <el-collapse-transition>
        <section v-show="artlistCreate" class="article-content">
         <all-art v-for="artpost in artposts"
            :key="artpost.id"
            :artpost="artpost"
          >
          </all-art>
        </section>
      </el-collapse-transition>
    </div>

    <div class="article-side-right">
    </div>
  </div>
</template>

<script>
  import articlelist from '../function/Art/articlelist.vue'
  import classify from '../function/Art/classify.vue'

  export default {
    name:'BlogArt',
    components: {
      'all-art': articlelist,
      'classify-art': classify
    },
    data:function() {
      return {
        artposts:[
          {id:1, title:'第一条测试', intro:'这是测试内容', imgJudge:true ,class:'前端', time:'2017-02-05', author: '努力\'向前', look:17},
          {id:2, title:'第一条测试', intro:'这是测试内容', imgJudge:false,class:'前端', time:'2017-02-05', author: '努力\'向前', look:17},
          {id:3, title:'第一条测试', intro:'这是测试内容', imgJudge:true,class:'前端', time:'2017-02-05', author: '努力\'向前', look:17},
          {id:4, title:'第一条测试', intro:'这是测试内容', imgJudge:true,class:'前端', time:'2017-02-05', author: '努力\'向前', look:17},
          // {id:5, title:'', intro:'', imgJudge:false,class:'前端', time:'2017-02-05', author: '努力\'向前', look:17},
          // {id:6, title:'', intro:'', imgJudge:false,class:'', time:'', author: '', look:''}
        ],
        rdartposts:[
          {id:1 ,con: '前端vue-cli搭建博客'},
          {id:2 ,con: '前端vue-cli搭建博客'},
          {id:3 ,con: '前端vue-cli搭建博客'},
        ],
        artlistCreate: false
      }
    },
    mounted() {
      this.artlistCreate = true;
    }
  }
</script>

<style>
  #article-auto {
    margin: 60px 0px 0px 0px;
    /* border: 1px yellow solid; */
    padding-top: 0px;
  }
  .article-side-left {
    position: fixed;
    top: 70px;
    left: 0%;
    width: 33%;
    /* border: 1px red solid; */
  }
  .article-body {
    margin: 0px 0px 0px 40%;
    /* border: 1px red solid; */
    background: rgba(255,255,255,1);
    padding-top: 1px;
    padding-bottom: 1px;
    min-height: 700px;
  }
  .article-content {
    margin: 20px 0px 0px 5%;
    width: 95%;
  }
  .side-left-title {
    margin-top: 400px;
    padding-top: 10px;
    width: 70%;
    font-size: 25px;
    color: white;
    /* border: 1px red solid; */
    text-align: center;
  }
  .side-left-con {
    text-align: center;
    font-size: 14px;
    width: 70%;
    /* border: 1px red solid; */
    color: white;
    letter-spacing: 2px;
  }
</style>



