<template>
  <li id="Article">
    <section v-if="!Judgeimg" class="Art-img">
      <img src="../../../assets/html.png" class="iconsize"/>

    </section>
    <div id="Art-body">

      <div class="Art-con">
        <header class="con-title">
          {{artpost.title}}
        </header>
        <div id="Art-end">
           <section class="end-classify">
             <aside style="">
               <i class="el-icon-price-tag">
                 <span style="font-size: 12px;margin-left: 5px;">{{artpost.class}}</span>
               </i>
             </aside>
           </section>
           <section class="end-mark">
             <section style="margin-top: 3px;">
               <i class="el-icon-user" style="margin-left: 15px;">
                 <span style="margin-left: 5px;">{{artpost.author}}</span>
               </i>
               <i class="el-icon-time" style="margin-left: 15px;">
                 <span style="margin-left: 5px;">{{artpost.time}}</span>
               </i>
               <i class="el-icon-view" style="margin-left: 15px;">
                 <span style="margin-left: 5px;">{{artpost.look}}</span>
               </i>
             </section>
           </section>
         </div>
        <article class="con-intro">
          {{artpost.intro}}
        </article>
      </div>
      <section class="end-more">
        <span>View More</span>
        <i class="el-icon-caret-right" :style="moreicon"></i>
      </section>

    </div>
    <section v-if="Judgeimg" class="Art-img">
      <img src="../../../assets/300295.jpg" class="img-size"/>
    </section>

  </li>
</template>

<script>
  export default {
    name:'articlelist',
    props:['artpost'],
    data:function() {
      return {
        Judgeimg: this.artpost.imgJudge ? true : false,
        moreicon: {
          float: 'right',
          margin: '3px 10px 0px 0px'
        }
      }
    }
  }
</script>

<style>
  #Article {
    margin: 10px 15% 50px 10px;
    list-style: none;
    background-color: #FFFFFF;
    padding: 10px;
    min-width: 330px;
    display: flex;
    border-radius: 10px;
    /* border-left: 1px #72767B solid; */
    border-top: 1px #72767B solid;
    transition: box-shadow 0.5s;
  }
  #Article:hover {
    box-shadow: 3px 3px 9px #757575;
  }
  #Art-body {
    margin: 10px 10px 0px 10px;
    /* border: 1px #FF00FF solid; */
    width: 55%;
  }
  .Art-img {
    margin-top: 10px;
    width: 45%;
    height: 230px;
    /* border: 1px red solid; */
    position: relative;
  }
  .Art-con {
    display: inline-block;
    margin: 5px 0px 0px 10px;
    width: 90%;
    /* border: 1px #303133 solid; */
    padding: 5px;
  }
  #Art-end {
    display: flex;
    margin: 10px 10px 0px 0px;
    /* border: 1px #D79922 solid; */
    height: 23px;
  }
  .con-title {
    /* border: 1px yellow solid; */
    width: 90%;
    font-size: 21px;
    color: #123C69;
    font-weight: bold;
    letter-spacing: 3px;
  }
  .con-intro {
    margin-top: 10px;
    /* border: 1px blue solid; */
    width: 95%;
    min-height: 100px;
    font-size: 14.5px;
    line-height: 24px;
    letter-spacing: 2px;
    text-align: left;
    color: #6B6E70;
  }
  .iconsize {
    position: absolute;
    top: 10px;
    left: 30%;
    width: 40%;
    height: 70%;
    /* border: 1px black solid; */
  }
  .img-size {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 97%;
    height: 90%;
    border-left: 3px #008000 solid;
    border-top: 3px #008000 solid;
    padding: 3px 0px 0px 3px;
  }
  .end-classify {
    flex: 0.3;
    color: #795548;
    border-radius: 5px;
  }
  .end-mark {
    flex: 1;
    color: #795548;
    font-size: 12px;
    /* border: 1px black solid; */
    margin-left: 5px;
    border-radius: 5px;
  }
 .end-more {
    background-image: linear-gradient(left, #8bc34a, #f9a825);
    border-radius: 10px;
    margin: 0px 53% 0px 4%;
    height: 30px;
    text-align: center;
    padding-top: 7px;
    font-size: 17px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    border: 2px white solid;
    transition: border-color 0.5s,color 0.5s;
  }
  .end-more:hover {
    background-image: linear-gradient(left, white, white);
    border-color:#f9a825;
    color: #8bc34a;
  }
</style>



