import Vue from 'vue'
import VueRouter from 'vue-router'
import BlogArt from '@/components/blog/BlogArt.vue'
import BlogHome from '@/components/blog/BlogHome.vue'
import BlogAbout from '@/components/blog/BlogAbout.vue'
import BlogLink from '@/components/blog/BlogLink.vue'
import BlogDiary from '@/components/blog/BlogDiary.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/bloghome',
    component: BlogHome
  },
  {
    path: '/blogart',
    component: BlogArt
  },
  {
    path: '/blogabout',
    component: BlogAbout
  },
  {
    path: '/bloglink',
    component: BlogLink
  },
  {
    path: '/blogdiary',
    component: BlogDiary
  },
  {
    path: '/',
    redirect: '/bloghome'
  }
];

var router = new VueRouter({
  routes:routes
});

export default router;



