<template>
  <div id="recd-article">
    <header class="recd-art-head">
      <i class="el-icon-s-order"></i>
      <span style="color: #1F2833;"><b>推荐文章</b></span>
    </header>
    <ul class="redc-art-location">
      <recd-art v-for="post in artposts"
        :key="post.id"
        :post="post"
      >
      </recd-art>
    </ul>
  </div>
</template>

<script>
  import recdart from '../../../function/Home/recdart.vue'


  export default {
    name:'recdart',
    components:{
      'recd-art': recdart
    },
    data:function(){
      return {
        artposts:[
          {id:1, title:'第一条测试', intro:'这是测试内容', imgJudge:true ,class:'前端'},
          {id:2, title:'第一条测试', intro:'这是测试内容', imgJudge:false,class:'前端'},
        ],
        showrecdart:false
      }
    },
    mounted() {
      this.showrecdart = true;
    }

  }
</script>

<style>
  #recd-article {
    /* border: 1px red solid; */
    margin: 100px 10% 0px 10%;
    padding: 10px;
  }
  .recd-art-head {
    /* border: 1px black solid; */
    margin: 10px 0px 0px 9%;
    font-size: 17px;
  }
  .redc-art-location {
    margin: 20px 0px 20px 1%;
    /* border: 1px green solid; */
  }
  .art-more {
    margin: 5px 13% 0px 70%;
    color: #182628;
    cursor: pointer;
    font-size: 17px;
  }
</style>



